<template>
<aside class="filter">
  <h2 class="filter__title">Фильтры</h2>

  <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
    <fieldset class="form__block">
      <legend class="form__legend">Цена</legend>
      <label class="form__label form__label--price" for="#id11">
        <input class="form__input" type="text" name="min-price" id="id11" v-model.number="currentPriceFrom">
        <span class="form__value">От</span>
      </label>
      <label class="form__label form__label--price" for="#id12">
        <input class="form__input" type="text" name="max-price" id="id12" v-model.number="currentPriceTo">
        <span class="form__value">До</span>
      </label>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend">Категория</legend>
      <label class="form__label form__label--select" for="#id13">
        <select class="form__select" type="text" name="category" id="id13" v-model.number="currentCategoryId">
          <option value="0">Все категории</option>
          <option :value="category.id" v-for="category in categories" :key="category.id">
            {{ category.title }}</option>
        </select>
      </label>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend">Цвет</legend>
      <ul class="colors">
        <li class="colors__item" v-for="color in colors" :key="color.id + color.title">
          <label class="colors__label">
            <input class="colors__radio sr-only" type="radio" name="color" :value="color.id" v-model="currentColor">
            <span class="colors__value" v-bind:style="{ 'background-color': color.code }">
            </span>
          </label>
        </li>
      </ul>
    </fieldset>

    <fieldset class="form__block">
      <legend class="form__legend">Объемб в ГБ</legend>
      <ul class="check-list">
        <li class="check-list__item">
          <label class="check-list__label" for="#id21">
<input class="check-list__check sr-only" type="checkbox" name="volume" value="8" checked=""
id="id21">
            <span class="check-list__desc">
              8
              <span>(313)</span>
            </span>
          </label>
        </li>
        <li class="check-list__item">
          <label class="check-list__label" for="#id22">
<input class="check-list__check sr-only" type="checkbox" name="volume" value="16" id="id22">
            <span class="check-list__desc">
              16
              <span>(461)</span>
            </span>
          </label>
        </li>
        <li class="check-list__item">
          <label class="check-list__label" for="#id23">
<input class="check-list__check sr-only" type="checkbox" name="volume" value="32" id="id23">
            <span class="check-list__desc">
              32
              <span>(313)</span>
            </span>
          </label>
        </li>
        <li class="check-list__item">
          <label class="check-list__label" for="#id24">
<input class="check-list__check sr-only" type="checkbox" name="volume" value="64" id="id24">
            <span class="check-list__desc">
              64
              <span>(313)</span>
            </span>
          </label>
        </li>
        <li class="check-list__item">
          <label class="check-list__label" for="#id25">
<input class="check-list__check sr-only" type="checkbox" name="volume" value="128" id="id25">
            <span class="check-list__desc">
              128
              <span>(313)</span>
            </span>
          </label>
        </li>
        <li class="check-list__item">
          <label class="check-list__label" for="#id26">
<input class="check-list__check sr-only" type="checkbox" name="volume" value="264" id="id26">
            <span class="check-list__desc">
              264
              <span>(313)</span>
            </span>
          </label>
        </li>
      </ul>
    </fieldset>

    <button class="filter__submit button button--primery" type="submit">
      Применить
    </button>
    <button class="filter__reset button button--second" type="button" @click.prevent="reset">
      Сбросить
    </button>
  </form>
</aside>
</template>

<script>
// import categories from '../data/categories';
// import colors from '../data/colors';
import axios from 'axios';
import {API_BASE_URL} from "../config";

export default {
  data() {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      currentColor: 0,

      categoriesData: null,
      colorsData: null,
    };
  },
  // props: ['priceFrom', 'priceTo', 'categoryId', 'color'],
  computed: {
    categories() {
      // return categories;
      return this.categoriesData ? this.categoriesData.items : [];
    },
    colors() {
      // return colors;
      return this.colorsData ? this.colorsData.items : [];
    },
  },
  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },
    priceTo(value) {
      this.currentPriceTo = value;
    },
    categoryId(value) {
      this.currentCategoryId = value;
    },
    color(value) {
      this.currentColor = value;
    },
  },
  methods: {
    submit() {
      this.$emit('update:priceFrom', this.currentPriceFrom);
      this.$emit('update:priceTo', this.currentPriceTo);
      this.$emit('update:categoryId', this.currentCategoryId);
      this.$emit('update:color', this.currentColor);
    },
    reset() {
      this.$emit('update:priceFrom', 0);
      this.$emit('update:priceTo', 0);
      this.$emit('update:categoryId', 0);
      this.$emit('update:color', 0);
    },
    loadCategories() {
      axios.get(API_BASE_URL + `/api/productCategories`)
      .then(response => this.categoriesData = response.data);
    },
    loadColors() {
      axios.get(API_BASE_URL + `/api/colors`)
      .then(response => this.colorsData = response.data);
    },
  },
  created() {
    this.loadCategories();
    this.loadColors();
  },
};
</script>
